export function SpecilSideBar({setSortSpecial}){
   // Hnadle Change
  function changeStateSortCategory(e) {
    setSortSpecial(e.target.id);
  }

  return (
    <div className="special-sort">
      <h2 className="title-sort">  </h2>
      <div className="form-groub">
        <input
          onChange={changeStateSortCategory}
          type="radio"
          name="sort"
          id="all"
        />
        <label htmlFor="all"> </label>
      </div>
      <div className="form-groub">
        <input
          onChange={changeStateSortCategory}
          type="radio"
          name="sort"
          id="low"
        />
        <label htmlFor="low">    </label>
      </div>
      <div className="form-groub">
        <input
          onChange={changeStateSortCategory}
          type="radio"
          name="sort"
          id="higt"
        />
        <label htmlFor="higt">    </label>
      </div>
    </div>
  );
}